@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");

:root {
  --primary: hsl(215, 51%, 70%);
  --secondary: hsl(178, 100%, 50%);
  --main-bg: hsl(216, 54%, 11%);
  --card-bg: hsl(216, 50%, 16%);
  --line: hsl(215, 32%, 27%);
  --fs: 18px;
  --font-family: "Outfit", sans-serif;
  --light: 300;
  --normal: 400;
  --bold: 600;
  --br: 10px;
  background-color: var(--main-bg);
  font-size: var(--fs);
  font-family: var(--font-family);
}

body {
  margin: 2rem;
}

.card {
  padding: 2rem;
  max-width: 30rem;
  margin: 2rem;
}

.img-container {
  position: relative;
  border-radius: var(--br);
}

.card__img {
  max-width: 100%;
  border-radius: var(--br);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 99%;
  transition: 0.25s ease-out;
  background-color: hsla(178, 100%, 50%, 0.486);
  border-radius: var(--br);
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay:hover {
  opacity: 1;
}

.card__title {
  color: hsl(0, 0%, 100%);
  font-weight: var(--normal);
  font-size: calc(var(--fs) * 1.5);
}

.card__title:hover {
  color: var(--secondary);
}

.card__body {
  color: var(--primary);
  font-weight: var(--light);
  font-size: calc(var(--fs) * 1.1);
}

.container {
  display: flex;
}

.card__value {
  font-weight: var(--bold);
  color: var(--secondary);
}

.card__time {
  margin-left: auto;
  color: var(--primary);
}
