@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap);
:root {
  --primary: hsl(215, 51%, 70%);
  --secondary: hsl(178, 100%, 50%);
  --main-bg: hsl(216, 54%, 11%);
  --card-bg: hsl(216, 50%, 16%);
  --line: hsl(215, 32%, 27%);
  --fs: 18px;
  --font-family: "Outfit", sans-serif;
  --light: 300;
  --normal: 400;
  --bold: 600;
  --br: 10px;
  background-color: hsl(216, 54%, 11%);
  background-color: var(--main-bg);
  font-size: 18px;
  font-size: var(--fs);
  font-family: "Outfit", sans-serif;
  font-family: var(--font-family);
}

body {
  margin: 2rem;
}

.card {
  padding: 2rem;
  max-width: 30rem;
  margin: 2rem;
}

.img-container {
  position: relative;
  border-radius: 10px;
  border-radius: var(--br);
}

.card__img {
  max-width: 100%;
  border-radius: 10px;
  border-radius: var(--br);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 99%;
  transition: 0.25s ease-out;
  background-color: hsla(178, 100%, 50%, 0.486);
  border-radius: 10px;
  border-radius: var(--br);
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay:hover {
  opacity: 1;
}

.card__title {
  color: hsl(0, 0%, 100%);
  font-weight: 400;
  font-weight: var(--normal);
  font-size: calc(18px * 1.5);
  font-size: calc(var(--fs) * 1.5);
}

.card__title:hover {
  color: hsl(178, 100%, 50%);
  color: var(--secondary);
}

.card__body {
  color: hsl(215, 51%, 70%);
  color: var(--primary);
  font-weight: 300;
  font-weight: var(--light);
  font-size: calc(18px * 1.1);
  font-size: calc(var(--fs) * 1.1);
}

.container {
  display: flex;
}

.card__value {
  font-weight: 600;
  font-weight: var(--bold);
  color: hsl(178, 100%, 50%);
  color: var(--secondary);
}

.card__time {
  margin-left: auto;
  color: hsl(215, 51%, 70%);
  color: var(--primary);
}

.Card_default-card__IC8Zu {
  font-family: var(--font-family);
  background-color: var(--card-bg);
  margin: 0 auto;
  max-width: 20rem;
  border-radius: var(--br);
  box-shadow: 0 15px 10px 20px rgba(12, 23, 41, 1),
    0 0 10px 30px rgba(12, 25, 44, 0.75);
}

